<template>
  <div>
    <!-- 面包屑导航部分 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/categories' }">商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容部分 -->
    <el-card>
      <!-- 添加分类按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" icon="el-icon-s-order" @click="addCateDialog">添加分类</el-button>
        </el-col>
      </el-row>
      <!-- 分类列表展示部分 -->
      <el-table :data="catesList" style="width:100%" row-key="cat_id" border :tree-props="{children: 'children'}">
        <el-table-column type="index" label="序号" width="60px">
        </el-table-column>
        <el-table-column prop="cat_name" label="分类名称">
        </el-table-column>
        <el-table-column label="是否有效">
          <template slot-scope="scope">
            <i class="el-icon-success" style="color:green" v-if="scope.row.cat_deleted === false"></i>
            <i class="el-icon-error" style="color:red" v-else></i>
          </template>
        </el-table-column>
        <el-table-column label="分类等级">
          <template slot-scope="scope">
            <!-- cat_level 为number类型  使用v-if来判断要显示的图标，最后一段不需要判断条件-->
            <el-tag v-if="scope.row.cat_level === 0 ">一级</el-tag>
            <el-tag v-else-if="scope.row.cat_level === 1 " type="success">二级</el-tag>
            <el-tag v-else type="warning">三级</el-tag>
          </template>
        </el-table-column>
        <!-- 操作分类信息部分 -->
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <!-- 编辑分类按钮 -->
            <el-button type="primary" icon="el-icon-edit" @click="editorCateDialog(scope.row.cat_id)" size="mini">编辑</el-button>
            <!-- 删除分类按钮 -->
            <el-button type="danger" icon="el-icon-delete" @click="delCate(scope.row.cat_id)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 添加分类的窗口 -->
      <el-dialog title="添加分类" :visible.sync="addCateDialogVisible" width="50%" @close="addCateClose">
        <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
          <el-form-item label="分类名称" prop="cat_name">
            <el-input v-model="addCateForm.cat_name" suffix-icon="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item label="父集分类">
            <!-- 级联选择器 -->
            <el-cascader label-width="80px" v-model="addCateForm.cat_pids" ref="addCascaderRef" :options="cateListOptions" placeholder="若不选择分类，默认新增为一级分类"
              @change="handleChange" :props="props" clearable filterable>
            </el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="addCateClose">取 消</el-button>
          <el-button type="primary" @click="addCate">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 编辑分类的窗口 -->
      <el-dialog title="编辑分类" :visible.sync="editorCateDialogVisible" width="50%" @close="editorCateClose">
        <el-form :model="editorCateForm" :rules="addCateFormRules" ref="editorCateFormRef" label-width="100px">
          <el-form-item label="分类名称" prop="roleName">
            <el-input v-model="editorCateForm.cat_name" suffix-icon="el-icon-user"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="editorCateClose">取 消</el-button>
          <el-button type="primary" @click="editorCate">确 定</el-button>
        </span>
      </el-dialog>
      <!-- page分页功能部分 -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageList.pagenum" :page-sizes="[5, 10, 20, 50]"
        :page-size="pageList.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>

  </div>
</template>
<script>
export default {
  data () {
    return {
      // 已有分类列表
      catesList: [],
      // 添加分类的表单信息
      addCateForm: {
        cat_name: '',
        cat_pids: [],
        cat_pid: 0,
        cat_level: 0
      },
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover',
        checkStrictly: 'ture'
      },
      cateListOptions: [],

      // 编辑分类的表单信息
      editorCateForm: {},
      pageList: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      // 多选树绑定的数据
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      // 多选结构树所有三级权限的id
      defKeys: [],
      // 要修改权限的id
      rolesId: '',
      // 分配权限的窗口
      setRightsDialogVisible: false,
      // 添加分类的窗口
      addCateDialogVisible: false,
      // 编辑分类的窗口
      editorCateDialogVisible: false,
      // 添加分类的校验规则
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getCatesList()
  },
  methods: {
    // 获取所有已有的分类列表
    async getCatesList () {
      const { data: res } = await this.$http.get('categories', {
        params: this.pageList
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catesList = res.data.result
      this.total = res.data.total
    },
    // 移除标签形式权限的模块
    removeRights (rightId, roles) {
      this.$confirm('此操作将删除该分类的对应权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`categories/${roles.id}`)
        if (res.meta.status !== 200) return this.$messgae.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // 不推荐直接调用获取权限方法，会刷新整个页面，使用返回来的值直接给形参，数据会回流到实参上。
        roles.children = res.data
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 当前显示数量模块
    handleSizeChange (size) {
      this.pageList.pagesize = size
      this.getCatesList()
    },
    // 翻页功能
    handleCurrentChange (current) {
      this.pageList.pagenum = current
      this.getCatesList()
    },
    // 关闭添加分类的窗口
    addCateClose () {
      this.addCateDialogVisible = false
      this.$refs.addCateFormRef.resetFields()
      // 清空级联选择
      this.$refs.addCascaderRef.panel.clearCheckedNodes()
    },
    // 关闭编辑窗口时重置表单
    editorCateClose () {
      this.$refs.editorCateFormRef.resetFields()
      this.editorCateDialogVisible = false
    },
    // 添加分类窗口时获取所有的分类
    async addCateDialog () {
      this.addCateDialogVisible = true
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.cateListOptions = res.data
    },
    // 添加分类的模块
    addCate () {
      this.$refs.addCateFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的分类信息！！')
        // // 判断分类所在的等级
        if (this.addCateForm.cat_pids.length > 0) {
          this.addCateForm.cat_level = (this.addCateForm.cat_pids.length)
          this.addCateForm.cat_pid = this.addCateForm.cat_pids[(this.addCateForm.cat_pids.length - 1)]
        } else {
          this.addCateForm.cat_level = 0
        }
        console.log(this.addCateForm)
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        console.log(res)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.addCateDialogVisible = false
        // 重置表单内容
        // this.$refs.addRoleFormRef.resetFields()
        this.getCatesList()
      })
    },
    // 级联选择器选中的值
    handleChange (value) {
      // console.log(value)
    },
    // 编辑分类窗口激活并获取需要编辑的分类信息
    async editorCateDialog (id) {
      this.editorCateDialogVisible = true
      const { data: res } = await this.$http.get(`categories/${id}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editorCateForm = res.data
    },
    // 编辑分类功能
    editorCate () {
      this.$refs.editorCateFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的分类信息！！')
        const { data: res } = await this.$http.put(`categories/${this.editorCateForm.cat_id}`, this.editorCateForm)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.editorCateDialogVisible = false
        // 重置表单内容
        // this.$refs.editorCateFormRef.resetFields()
        this.getCatesList()
      })
    },
    // 删除分类按钮
    delCate (id) {
      this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`categories/${id}`)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.getCatesList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }

  }
}
</script>
<style lang="less" scoped>
</style>
