<template>
  <div>
    <!-- 面包屑导航部分 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/params' }">商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容部分 -->
    <el-card>
      <!-- 警告alert区域 -->
      <el-row>
        <el-col>
          <el-alert title="注意：只允许为第三级分类设置相关参数！否则无法继续操作！！！" type="warning" show-icon>
          </el-alert>
        </el-col>
      </el-row>
      <!-- 级联选择区 -->
      <el-row class="cascader">
        <el-col>
          <span>请选择商品分类：</span>
          <el-cascader v-model="cat_pids" ref="cascaderRef" :options="catesList" placeholder="必须选择到三级分类" @change="handleChange" :props="props" clearable
            filterable>
          </el-cascader>
        </el-col>
      </el-row>
      <!-- 标签页模块 -->
      <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="动态参数" name="many" :disabled='isDisabled'>
          <!-- 添加分类按钮 -->
          <el-row>
            <el-col>
              <el-button type="primary" icon="el-icon-s-tools" @click="addParamsDialog" :disabled='isDisabled'>添加参数</el-button>
            </el-col>
          </el-row>
          <!-- 分类列表展示部分 -->
          <!-- 不是树形表格，不要指定row-key，会出现奇奇怪怪的错误 -->
          <el-table :data="paramsList" style="width:100%" border>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- 动态渲染标签式tag -->
                <el-tag :key="i" v-for="(item,i) in scope.row.attr_vals" closable :disable-transitions="false" @close="handleClose(scope.row,item)">
                  {{item}}
                </el-tag>
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ 新的标签</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="序号" width="60px">
            </el-table-column>
            <el-table-column prop="attr_name" label="参数名称">
            </el-table-column>
            <!-- 操作分类信息部分 -->
            <el-table-column label="操作" width="300px">
              <template slot-scope="scope">
                <!-- 编辑分类按钮 -->
                <el-button type="primary" icon="el-icon-edit" @click="editorParamsDialog(scope.row)" size="mini">编辑</el-button>
                <!-- 删除分类按钮 -->
                <el-button type="danger" icon="el-icon-delete" @click="delParams(scope.row.attr_id)" size="mini">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only" :disabled='isDisabled'>
          <!-- 添加分类按钮 -->
          <el-row>
            <el-col>
              <el-button type="primary" icon="el-icon-user" @click="addParamsDialog" :disabled='isDisabled'>添加属性</el-button>
            </el-col>
          </el-row>
          <!-- 分类列表展示部分 -->
          <el-table :data="attrsList" style="width:100%" border>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- 动态渲染标签式tag -->
                <el-tag :key="i" v-for="(item,i) in scope.row.attr_vals" closable :disable-transitions="false" @close="handleClose(scope.row,item)">
                  {{item}}
                </el-tag>
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ 新的标签</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" label="序号" width="60px">
            </el-table-column>
            <el-table-column prop="attr_name" label="参数名称">
            </el-table-column>
            <!-- 操作分类信息部分 -->
            <el-table-column label="操作" width="300px">
              <template slot-scope="scope">
                <!-- 编辑分类按钮 -->
                <el-button type="primary" icon="el-icon-edit" @click="editorParamsDialog(scope.row)" size="mini">编辑</el-button>
                <!-- 删除分类按钮 -->
                <el-button type="danger" icon="el-icon-delete" @click="delParams(scope.row.attr_id)" size="mini">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!-- 添加参数属性窗口 -->
      <el-dialog :title="'添加'+isAcitveName" :visible.sync="addParamsDialogVisible" width="50%" @close="addParamsClose">
        <el-form :model="addParamsForm" :rules="addParamsFormRules" ref="addParamsFormRef" label-width="100px">
          <el-form-item :label="isAcitveName+'名称'" prop="attr_name">
            <el-input v-model="addParamsForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="addParamsClose">取 消</el-button>
          <el-button type="primary" @click="addParams">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改参数属性窗口 -->
      <el-dialog :title="'修改'+isAcitveName" :visible.sync="editorParamsDialogVisible" width="50%" @close="editorParamsClose">
        <el-form :model="editorParamsForm" :rules="addParamsFormRules" ref="editorParamsFormRef" label-width="100px">
          <el-form-item :label="isAcitveName+'名称'" prop="attr_name">
            <el-input v-model="editorParamsForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer">
          <el-button @click="editorParamsClose">取 消</el-button>
          <el-button type="primary" @click="editorParams">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 默认选中的tabs表格页,选中的tabs会改变此属性
      activeName: 'many',
      // 所有的分类数据
      catesList: [],
      // 获取到的参数列表
      paramsList: [],
      // 获取到的属性列表
      attrsList: [],
      // 选中的分类
      cat_pids: [],
      // 第三级的id
      thirdCat: 0,
      // 级联选择器绑定数据，
      setAttrId: 0,
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 添加分类的参数属性的表单
      addParamsForm: {
        attr_name: ''
      },
      // 修改分类的参数属性的表单
      editorParamsForm: {
        attr_name: ''
      },
      // 添加分类属性参数的窗口
      addParamsDialogVisible: false,
      editorParamsDialogVisible: false,
      // 添加分类属性参数的窗口校验
      addParamsFormRules: {
        attr_name: [
          { required: true, message: '请输入参数/属性名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
        ]
      }
      // 可编辑标签属性区域
      // inputValue: '',
      // inputVisible: false
    }
  },
  created () {
    this.getCatesList()
  },
  computed: {
    // 禁用按钮
    isDisabled () {
      if (this.cat_pids.length === 3) {
        return false
      } else {
        return true
      }
    },
    // 计算弹出的窗口名称
    isAcitveName () {
      if (this.activeName === 'many') {
        return '参数'
      } else {
        return '属性'
      }
    }
  },
  methods: {
    // 获取所有三级分类
    async getCatesList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catesList = res.data
    },
    // 级联选择器值变化检测
    handleChange (value) {
      if (this.cat_pids.length !== 3) {
        this.cat_pids = []
        return this.$message.error('请选择三级商品分类')
      }
      // 抽离动态参数和属性的方法
      this.thirdCat = value[value.length - 1]
      this.getAttrList()
    },
    // 动态获取参数属性
    async getAttrList () {
      const { data: res } = await this.$http.get(`categories/${this.thirdCat}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      res.data.forEach(item => {
        // 空字符串切割也会返回一个不为空的字符串数组
        // 不要写错了item.attr_vals循环对象
        // 遍历的时候把新增的按钮表单的属性赋值到对象中,防止输入框联动
        item.inputValue = ''
        item.inputVisible = false
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      if (this.activeName === 'many') {
        this.paramsList = res.data
      } else {
        this.attrsList = res.data
      }
    },

    // 关闭添加窗口
    addParamsClose () {
      this.addParamsDialogVisible = false
      this.$refs.addParamsFormRef.resetFields()
    },
    //
    editorParamsClose () {
      this.editorParamsDialogVisible = false
      this.$refs.editorParamsFormRef.resetFields()
    },
    // 添加功能
    addParams () {
      this.$refs.addParamsFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的参数/属性！！')
        const { data: res } = await this.$http.post(`categories/${this.thirdCat}/attributes`, {
          attr_name: this.addParamsForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.addParamsDialogVisible = false
        // 不需要重置表单内容
        // this.$refs.editorFormRef.resetFields()
        this.getAttrList()
      })
    },
    // 添加窗口
    addParamsDialog () {
      this.addParamsDialogVisible = true
    },
    // 切换窗口时的响应函数
    handleClick (tab, event) {
      this.getAttrList()
    },
    // 删除动态参数
    handleClose (row, item) {
      // indexof存在则返回数组对应的下标，splice删除对应的下标
      row.attr_vals.splice(row.attr_vals.indexOf(item), 1)
      this.setAttr(row)
    },
    // 点击显示输入框
    showInput (row) {
      row.inputVisible = true
      // 回调函数，等dom渲染好了再执行这个重置模块
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 抽取增删标签的请求事件
    async setAttr (row) {
      const { data: res } = await this.$http.put(`categories/${this.thirdCat}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: this.activeName,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 空字符串切割也会返回一个不为空的字符串数组
      row.attr_vals = res.data.attr_vals ? res.data.attr_vals.split(' ') : []
    },
    // 标签式输入框，失焦或enter后的事件
    handleInputConfirm (row) {
      const inputValue = row.inputValue.trim()
      if (inputValue) {
        row.attr_vals.push(inputValue)
        this.setAttr(row)
      }
      row.inputVisible = false
      row.inputValue = ''
    },
    async editorParamsDialog (row) {
      this.editorParamsDialogVisible = true
      this.editorParamsForm.attr_vals = row.attr_vals.join(' ')
      const { data: res } = await this.$http.get(`categories/${this.thirdCat}/attributes/${row.attr_id}`, {
        params: {
          attr_sel: this.activeName,
          attr_vals: row.attr_vals.join(' ')
        }

      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editorParamsForm.attr_name = res.data.attr_name
      this.setAttrId = row.attr_id
    },
    editorParams () {
      this.$refs.editorParamsFormRef.validate(async callback => {
        if (!callback) return this.$message.error('请输入合法的参数/属性！！')
        const { data: res } = await this.$http.put(`categories/${this.thirdCat}/attributes/${this.setAttrId}`, {
          attr_name: this.editorParamsForm.attr_name,
          attr_sel: this.activeName,
          // 编辑的时候不能丢了子参数 attr_vals
          attr_vals: this.editorParamsForm.attr_vals
        })
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.editorParamsDialogVisible = false
        // 不需要重置表单内容
        // this.$refs.editorFormRef.resetFields()
        this.getAttrList()
      })
    },
    delParams (id) {
      this.$confirm('此操作将永久删除该参数/属性, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`categories/${this.thirdCat}/attributes/${id}`)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.getAttrList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
// 不使用scoped会出现样式权重不够
.el-cascader {
  margin: 20px 0;
  width: 240px;
}
.el-tabs {
  margin: 20px 0;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
